//colors 
$color_chardonnay_approx: #ffd08a;
$color_hit_pink_approx: #ffa376;
$color_contessa_approx: #d26578;
$color_eminence_approx: #682a84;
$color_lucky_point_approx: #241668;
$white: white;
$white_60: rgba(255,255,255,0.6);
$black_80: rgba(0,0,0,0.8);

//fonts
$font_0: "Roboto", "Helvetica", "Arial", sans-serif;

body {
	font-family: $font_0;
	height: 100vh;
	width: 100%;
	background: purple;
	padding: 0;	
	margin: 0;
	box-sizing: border-box;
}
h2 {
	color: white;
	font-family: $font_0;
}
ul {
	li {
		list-style-type: none;
	}
	&.nav-wrapper li {
		z-index: 1;
	}
}

.global-nav {
	position: relative;
	position: fixed;
	width: 100%;
	ul {
		padding: 0;
		margin: 0;
	}
}
.nav-inner {
	// padding: 5px 0;
	margin: 0 30px;
}
.nav-wrapper {
	display: flex;
	align-items: center;
	flex-flow: row wrap;
	justify-content: space-between;
	position: relative;
	a:hover {
		color: $white_60;
	}
}
.navsection-logo {
	* {
		fill: $white;
	}
	&:hover {
		fill-opacity: 0.6;
	}
	svg {
		width: 100px;
		height: 50px;
	}
}
.navsection-primary {
	display: none;
	a {
		font-size: 12px;
		margin: 0;
		padding: 0 8px;
	}
	ul {
		display: flex;
		font-size: 12px;
		padding: 0 8px;
	}
}
.navsection-mobile { 
	display: none;
	position: relative;
	display: flex;
}
.mobilemenu-wrapper {
	display: flex;
	height: 28px;
	width: 34px;
	cursor: pointer;
	z-index: 500;
	//Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
	transition: opacity 0.25s ease;
	&:hover {
		opacity: 0.6;
	}
	h2 {
		font-size: 0 !important;
	}
	&.active {
		.bar-1 {
			//Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
			transform: translateY(11px) translateX(0) rotate(45deg);
			background: $white;
		}
		.bar-2 {
			opacity: 0;
			background: $white;
		}
		.bar-3 {
			//Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
			transform: translateY(-11px) translateX(0) rotate(-45deg);
			background: $white;
		}
	}
	span {
		background: $white;
		border: none;
		height: 4px;
		width: 100%;
		position: absolute;
		//Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
		transition: all 0.35s ease;
		cursor: pointer;
		&:nth-of-type(1) {
			top: 0;
		}
		&:nth-of-type(2) {
			top: 11px;
		}
		&:nth-of-type(3) {
			top: 22px;
		}
	}
}
.overlay {
	// text-align: center;
	// padding-top: 50px;
	background: $black_80;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 0;
	visibility: hidden;
	//Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
	transition: visibility 0.35s, height 0.35s;
	overflow: auto;
	&.open {
		visibility: visible;
		height: 100%;
	}
	.overlay-flex-wrapper {
		// height: 100%;
	    margin-top: 80px;
	    margin-bottom: 80px;
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
		align-items: flex-start;
		align-content: flex-start;
		h2 {
			font-weight: 400;
			border-bottom: 1px solid white;
			padding-bottom: 9px;
		}
		a {
			font-size: 1.1rem;
			line-height: 1.5;
			font-weight: 300;
			color: hsla(0, 0%, 83%, 1);
		}
		li {
			padding: 8px;
		}

		ul {
			flex: 1 0 200px;
		    padding: 0 29px;	
		}
	}
}
a {
	color: $white;
	text-decoration: none;
}
// right: 0;
.global-nav a {
	color: $white;
	text-decoration: none;
}
